import React, {useState} from "react";
import PropTypes from 'prop-types';
import classNames from "classnames";

export const PizzaBlock = ({id,imageUrl,price,name,types,sizes,onClickAddPizzaCart,totalPizzasBtn}) =>{
    const [activeView, setActiveView] = useState(types[0]);
    const [activeSize, setActiveSize] = useState(sizes[0]);
    const availableName = ["тонкое", "траиционное"];
    const availableSize = [26, 30, 40];
    const onCLickView = (index) =>{
        setActiveView(index)
    };
    const onCLickSize = (index) =>{
        setActiveSize(index)
    };

    const handleClickBlockPizza = () => {
        const obj = {
            id,imageUrl,price,name,types,sizes
        };
        onClickAddPizzaCart(obj)

    };

    return(
        <div className="pizza-block">
            <img
                className="pizza-block__image"
                src={imageUrl}
                alt="Pizza"
            />
            <h4 className="pizza-block__title">{name}</h4>
            <div className="pizza-block__selector">
                <ul>
                    {availableName.map((view,index)=>(
                        <li onClick={()=>{onCLickView(index)}}
                            key={`${index}_${view}`}
                            className={classNames({
                                "active": index === activeView,
                                "disable": !types.includes(index)
                            })}
                        >{view}</li>
                    ))}
                </ul>
                <ul>
                    {availableSize.map((size,index)=>(
                        <li onClick={()=>onCLickSize(index)}
                            key={`${index}_${size}`}
                            className={classNames({
                            "active": index === activeSize,
                             "disable": !sizes.includes(size)
                        })}>{size}см</li>
                    ))}
                </ul>
            </div>
            <div className="pizza-block__bottom">
                <div className="pizza-block__price">{price} руб</div>
                <div onClick={handleClickBlockPizza} className="button button--outline button--add">
                    <svg
                        width="12"
                        height="12"
                        viewBox="0 0 12 12"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                    >
                        <path
                            d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
                            fill="white"
                        />
                    </svg>
                    <span>Добавить</span>
                    <i>{totalPizzasBtn}</i>
                </div>
            </div>
        </div>
    )

};

PizzaBlock.propTypes = {
    id: PropTypes.number.isRequired,
    name : PropTypes.string.isRequired,
    imageUrl: PropTypes.string.isRequired,
    price: PropTypes.number.isRequired,
    types: PropTypes.arrayOf(PropTypes.number).isRequired,
    sizes: PropTypes.array.isRequired,
    onClickAddPizzaCart: PropTypes.func,
    totalPizzasBtn: PropTypes.number.isRequired
};

PizzaBlock.defaultProps = {
    types:[],
    sizes:[],
    totalPizzasBtn:0
};
